.centerPannel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}
.loginPannel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    background-color: #a6e7cd80;
    padding: 60px;
    position: relative;
    top: 30px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.18);
}
.show-grid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.loginPannelHeading {
    font-family: Helvetica Neue;
    font-size: 28px;
    font-weight: 300;
    padding-bottom: 18px;
}
.charecter {
    height: 260px;
    position: relative;
    left: 260px;
    bottom: 140px;
}
.aliasInput {
    position: relative;
    padding-left: 20px;
    margin-bottom: 10px;
    height: 40px;
    background: #36c2b467;
    border: none;
    border-radius: 5px;
    /* border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid #36C2B49e; */
    width: 240px;
    color: #008577;
    font-size: 14px;
    outline: none;
}
.aliasInput::placeholder {
    color: #0497969e;
    font-size: 13px;
}
.buttonClick {
    background: none;
    height: 40px;
    width: 160px;
    border: 2px solid #008577;
    color: #008577;
    font-size: 14px;
    -webkit-transition-duration: 0.5s; /* Safari */
    transition-duration: 0.5s;
    cursor: pointer;
    border-radius: 3px;
    margin-top: 12px;
    margin-right: 3px;
}
.buttonClick:hover {
    background-color: #008577;
    color: white;
}
