body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



.LandingPagePreview {
  background-color: #f5f5f5;
  padding: 16px;
  margin: 7px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}

.LandingPagePreview a {
  text-decoration: none;
  color: #669fd5;
  font-size: 15px;
}

.LandingPagePreviewItem {
  padding: 5px;
  margin-left: 20px;
  text-decoration: underline;
}

.LandingPagePreviewInput {
  padding-left: 20px;
  height: 40px;
  background: #c7cac967;
  border: none;
  border-radius: 5px;
  width: 900px;
  margin-right: 9px;
  color: #000000;
  font-size: 14px;
  outline: none;
}



.LandingPagePreviewAddUrl {
  background: none;
  height: 40px;
  width: 160px;
  border: 2px solid #1b2531;
  color: #1b2531;
  font-size: 14px;
  -webkit-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
  transition-duration: 0.5s;
  cursor: pointer;
  border-radius: 3px;
  margin-left: 30px;
}